@import 'reset-css';

html {
  font-size: 62.5%;
}

body * {
  font-size: 1.6rem;
  font-family: 'IBM Plex Sans', sans-serif;
  color: rgb(60, 60, 60);
}

main {
  position: relative;
  background: #efefef;
  // height: 100vh;
  padding-top: 200px;
  padding-bottom: 100px;
}

article {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;

  .content {
    background: white;
    padding: 40px 40px;
  }
}

header {
  padding: 20px 40px;
  text-align: center;
  background: rgb(60, 60, 60);
  box-shadow: 0 5px 5px #ccc;
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

h1 {
  font-size: 5.2rem;
  line-height: 6rem;
  margin-bottom: 6rem;
}

h2 {
  font-size: 3.2rem;
  line-height: 4rem;
  color: rgb(160, 160, 160);
  margin-bottom: 4rem;
}

a {
  font-size: inherit;
  line-height: inherit;
  text-decoration: underline;
  color: inherit;
}

p {
  font-size: 2rem;
  line-height: 2.8rem;
  margin-bottom: 2.8rem;
}

strong {
  font-weight: 700;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
}

button {
  background: rgb(35, 255, 109);
  -webkit-appearance: none;
  outline: none;
  border: 0;
  border-radius: 3px;
  padding: 12px 20px;
  margin-right: 20px;
  cursor: pointer;
  margin-bottom: 10px;

  &:focus,
  &:active {
    background: rgb(25, 245, 100);
  }
}

#meta > .content {
  padding-top: 20px;
  padding-bottom: 20px;
  background: rgb(35, 255, 109);
  text-align: right;
}

#meta-slot {
  position: relative;
}

.logo {
  width: 100px;
  height: 100px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-color: white;
  border-radius: 50px;
  box-shadow: 0px 0px 10px 0 rgb(60,60,60);
  // transform: translateY(-75px);
  position: absolute;
  top: -75px;
  left: 0;
}

.meta-contributors,
.meta-creator {
  // float: right;
  display: inline-block;
  font-size: 2.4rem;
  color: white;
}

.meta-creator {
  padding-right: 30px;
}

#images-slot {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-column-gap: 20px;

  figure {
    grid-column-end: span 4;

    img {
      max-width: 100%;
    }
  }
}

#buttons {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
}

@media screen and (max-width: 650px) {
  header img {
    max-width: 150px;
  }

  h1 {
    font-size: 3.2rem;
    line-height: 4rem;
    margin-bottom: 4rem;
  }

  h2 {
    font-size: 2.6rem;
    line-height: 3.2rem;
    margin-bottom: 3.2rem;
  }

  p {
    font-size: 1.6rem;
    line-height: 2.4rem;
    margin-bottom: 2.4rem;
  }

  article .content {
    // // background: white;
    padding: 20px 20px;
  }
}
